
import { defineComponent, PropType } from "vue";
import { IonGrid, IonRow, IonCol, IonButton } from "@ionic/vue";
import { isArray } from "lodash";
export default defineComponent({
  name: "HisResultCard",
  components: {
    IonGrid,
    IonRow,
    IonCol,
    IonButton
  },
  props: {
    icon: {
      required: false,
    },
    items: {
      type: Object as PropType<any[]>,
      required: true,
    },
  },
  methods: {
    printLabel(date: any) {
      this.$emit("onPrint", date);
    },
    showMore(date: any) {
      this.$emit("onDetails", date);
    },
    formatAdherence(vals: any) {
      console.log(vals);
      if (isArray(vals)) {
            const f = [...vals];
            if (isArray(f)) {
              let j = "";
              f.forEach((element) => {
                j += `${element.join(":")} (%), `;
              });
              return j;
            }
            return f;
          } else {
            return vals;
          }
    }

  },
});
