
import { IonApp, IonRouterOutlet, IonButton } from '@ionic/vue';
import { defineComponent, ref, watch } from 'vue';
import ApiClient, { ApiBusEvents } from "@/services/api_client"
import EventBus from "@/utils/EventBus"
import { toastWarning, toastDanger, alertConfirmation } from './utils/Alerts';
import { useRoute } from 'vue-router';
/** Nprogress */
import 'nprogress/nprogress.css'
import nprogress from 'nprogress'
import router from '@/router/index';

export default defineComponent({
  name: 'App',
  components: {
    IonApp,
    IonButton,
    IonRouterOutlet
  },
  setup() {
    const apiOk = ref(true)
    const route = useRoute()
    const notConfigPage = ref(true)
    
    nprogress.configure({ 
      easing: 'ease', 
      speed: 870, 
      trickleSpeed:1 
    })

    function refresh() {
      location.reload()
    }

    // Check if the API is back ONLINE if its down
    setInterval(() => {
      if (!apiOk.value && route.name != 'API host settings') {
        ApiClient.healthCheck()
      }
    }, 8000)

    watch(route, (route) => 
      notConfigPage.value = route.name != 'API host settings',
      {
        immediate: true
      }
    )

    EventBus.on(
      ApiBusEvents.BEFORE_API_REQUEST, 
      () => nprogress.start()
    )

    EventBus.on(
      ApiBusEvents.AFTER_API_REQUEST, 
      async (res: any) => {
        if (!apiOk.value) {
          apiOk.value = true
          const confirm = await alertConfirmation(
            'Do you want to refresh the page?',
            'API connection is back'
          )
          if (confirm) refresh()
        }
        if (res && res.status === 401 && route.name != 'Login') {
          router.push('/login')
        } else if (res.status >= 500) {
          const { error, exception } = await res.json();
          toastDanger(`${error} - ${exception}`);
        }
        nprogress.done()
      }
    )

    EventBus.on(
      ApiBusEvents.ON_API_CRASH, 
      () => {
        if (apiOk.value) {
          toastWarning('Unable to reach api. You can fix the error below')
          apiOk.value = false
        }
        nprogress.done()
      }
    )
    return {
      apiOk,
      refresh,
      notConfigPage
    }
  },
});
